@import url('./_small/popup__container_small.css');
@import url('./_medium/popup__container_medium.css');
@import url('./_large/popup__container_large.css');

.popup__container {
    position: relative;
    width: 430px;
    background-color: white;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 34px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }

  @media screen and (max-width: 700px) {
    .popup__container {
      max-width: 280px;
    }
  }
